//El componente calculatePageRank es una implementacin del 
// algoritmo PageRank, que es utilizado para asignar una 
// puntuacin de importancia a cada pgina en un grafo de 
// pginas web (o nodos en un grafo en general). Este algoritmo 
// fue desarrollado originalmente por los fundadores de Google,
//  Larry Page y Sergey Brin, para clasificar pginas web en 
// los resultados de bsqueda.



const calculatePageRank = (pages, iterations = 100, dampingFactor = 0.85) => {
    const pageRank = {};
    const numPages = Object.keys(pages).length;
  
    // Inicializar PageRank: asignar a cada pgina un valor inicial de 1/nmero de pginas
    for (const page in pages) {
      pageRank[page] = 1 / numPages;
    }
  
    // Realizar mltiples iteraciones para calcular el PageRank
    for (let i = 0; i < iterations; i++) {
      const newPageRank = {};
  
      // Calcular el nuevo PageRank para cada pgina
      for (const page in pages) {
        // Comenzar con el factor de amortiguamiento
        newPageRank[page] = (1 - dampingFactor) / numPages;
  
        // Sumar contribuciones de otras pginas que enlazan a esta pgina
        for (const otherPage in pages) {
          // Verificar si 'otherPage' enlaza a 'page'
          if (pages[otherPage].includes(page)) {
            // Actualizar el PageRank basado en el PageRank de 'otherPage' y su nmero de enlaces salientes
            newPageRank[page] += dampingFactor * (pageRank[otherPage] / pages[otherPage].length);
          }
        }
      }
  
      // Actualizar el PageRank con los nuevos valores calculados
      Object.assign(pageRank, newPageRank);
    }
  
    // Devolver el PageRank final de todas las pginas
    return pageRank;
  };
  
  export default calculatePageRank;